import React from 'react';

import './Project.css';

import axios from 'axios';

import TextField from '@material-ui/core/TextField';
import DateFnsUtils from '@date-io/date-fns';
import Button from '@material-ui/core/Button';
import 'date-fns';
import Grid from '@material-ui/core/Grid';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  button: {
    margin: theme.spacing(1),
  },
  input: {
    display: 'none',
  },
}));

function create_project(){
  axios.post('http://localhost:8080/Burari/project', {
    name: document.getElementById('standard-name').value,
    date: document.getElementById('date-picker-inline').value
  }).then( function(response) {
    console.log(response)
  }).catch(function(error) {
    console.log(error);
  });
}

function SubBtn(){
  const classis = useStyles();

  return (
    <div>
      <Button
        className={classis.button}
        variant="contained"
        color="primary"
        onClick={create_project}>
        생성
      </Button>
      <Button
        className={classis.button}
        color="secondary"
        variant="contained">
        취소
      </Button>
    </div>
  );
}

export default class Project extends React.Component{
  constructor(props){
    super(props);

    this.state = {
      setSelectedDate : new Date()
    }

    this.handleDateChange = this.handleDateChange.bind(this);
  }

  handleDateChange = (date) => {
    this.setState({
      setSelectedDate: date
    });
  }

  render(){
    return (
      <div className="body">
        <div className="formfield">
          <div className="ItemMargin">
            <TextField
              id="standard-name"
              label="프로젝트 이름"
              margin="normal"
            />
          </div>
          <div className="ItemMargin">
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="yyyy-MM-dd"
                margin="normal"
                id="date-picker-inline"
                label="만료일자"
                value={this.state.setSelectedDate}
                onChange={this.handleDateChange}
                KeyboardButtonProps={{
                'aria-label': 'change date',
                }}
              />
            </MuiPickersUtilsProvider>
          </div>
        </div>
        <div className="submitButton">
          <SubBtn/>
        </div>
      </div>
    )
  }
}
