import React from 'react';

import Login from './login/Login';
import Project from './project/Project';
import Header from './header/Header';
import Home from './home/Home';
import './App.css';

import { BrowserRouter, Route } from "react-router-dom";

class App extends React.Component {

  constructor(props){
    super(props);

    this.state = {
      content: "login"
    }
  }

  maincontent(){
    var contentList = {
      "home": <Home/>,
      "project": <Project/>,
      "login": <Login/>
    }

    return contentList[this.state.content];
  }

  ComponentChange(url){
    var component = this.urlChecker(url);

    if(component !== this.state.content){
      this.setState({
        content: component
      });
    }
  }

  componentWillMount(){
    if(window.sessionStorage.getItem("BurariID") != null) {
      this.setState({
        content: "home"
      })
    } else {
      return;
    }

    window.onpopstate  = (e) => {
      this.ComponentChange(window.location.hash.substring(1));
    }
  }

  urlChecker(url){
    if(url.lastIndexOf("/") > 0){
      return url.substring(0, url.lastIndexOf("/"));
    } else {
      return url;
    }
  }

  render(){
    return (
      <div className="App">
        <Header/>
        {this.maincontent()}
      </div>
    );
  }
}

export default App;
